@import url(https://fonts.googleapis.com/css2?family=Rajdhani:wght@500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Permanent+Marker&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
  margin: 0;
  padding: 0;
  font-weight: 600;
  transition: 0.3s;
  font-family: 'Rajdhani', sans-serif;
}
button{
  padding: 3px 10px;
  border: none;
  outline: none;
  cursor: pointer;
}
input, textarea{
  border: none;
  outline: none;
  padding: 5px 10px;
  border-radius: 3px;
}
.center{
  display: flex;
  align-items: center;
  justify-content: center;
}
.black{
  background: rgba(0, 0, 0, 1);
  transition: 0.5s;
}
.App {
  width: 100%;
  position: relative;
  
}
.App .head {
  position: fixed;
  top: 0;
  width: 100%;
  transition: 0.5s;
}
.head_line{
  color: white;
  text-align: center;
  font-family: 'Permanent Marker', cursive;
  font-size: 30px;
}
.hello{
  font-size: 15px;
  font-style: italic;
  color: white;
  display: flex;
  align-items: center;
}
.hello span{
  font-size: 20px;
  font-weight: 800;
  color: #479CFF;
  margin-left: 8px;
}
.App .head header{
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 30px 70px;
}
.App .head header img{
  width: 70px;
  height: 70px;
}
.App .head button{
  background: #479CFF;
  padding: 5px 10px 5px 20px;
  color: white;
  border: none;
  outline: none;
  border-radius: 5px;
}
.app_layer, .app_layer2{
  width: 100%;
  height: 100%;
  background: url(/Image-editing-bootcamp/static/media/bg.66be0c6c.jpg);
  background-size: cover;
  background-position: center;
  position: fixed;
  top: 0;
  z-index: -1;
}
.app_layer2{
  background: linear-gradient(rgba(0, 0, 0, 0.7), rgba(0, 0, 0, 0.4));
}
@media only screen and (max-width: 550px){
  .hello{
    flex-direction: column;
    align-items: flex-start;
  }
  .hello span{
    margin-left: 0;
  }
  .head_line{
      font-size: 20px;
  }
}
@media only screen and (max-width: 700px){
  .App .head header{
    margin: 20px;
  }
}
@media only screen and (min-width: 1000px){
  .head_line{
      font-size: 35px;
  }
}

.login_con{
    height: 100vh;
    width: 100%;
}
.login{
    background-color: white;
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    width: -webkit-min-content;
    width: -moz-min-content;
    width: min-content;
    box-shadow: 4px 4px 10px grey;
    border-radius: 10px;
}
.login img{
    width: 300px;
    border-radius: 10px;
}
.login form{
    padding: 30px 30px 30px 0px;
}
.form_title{
    font-size: 25px;
    margin-bottom: 20px;
}
.login form input{
    width: 250px;
    height: 30px;
    border: none;
    outline: none;
    border-bottom: 2px solid #479CFF;
    margin: 12px 0;
}
.login form .button{
    border: none;
    outline: none;
    background: rgb(94, 247, 196);
    padding: 5px 15px;
    margin-top: 20px;
}
.form_control{
    display: flex;
    align-items: center;
    font-size: 12px;
    margin-top: 20px;
}
.form_control button{
    font-size: 15px;
    font-weight: 800;
    color: #479CFF;
    cursor: pointer;
    background: none;
    border:none;
    outline: none;
}

@media only screen and (max-width: 600px){
    .login{
        flex-direction: column;
    }
    .login form{
        padding: 0;
        margin: 30px;
        margin-top: 0;
    }
}
.tasks{
    margin-top: 130px;
}
.tasks_con{
    padding: 30px 0;
}
/* .tasks_head{
    position: fixed;
    width: 100%;
} */
.tasks_head div{
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 30px 70px 0 70px;
}
.tasks_head input{
    width: 120px;
}
.task_coll{
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
    padding: 0 70px;
}
.task_coll .empty{
    color: white;
    width: 300px;
}
.task_coll .empty img{
    border-radius: 10px;
    width: 100%;
    margin-top: 20px;
}
.black{
    background: rgba(0, 0, 0, 1);
    transition: 0.5s;
  }

@media only screen and (max-width: 700px){
    .tasks{
        margin-top: 150px;
    }
    .tasks_head div{
        padding: 20px;
    }
    .task_coll{
        padding: 20px;
    }
}
.task{
    width: 30%;
    margin: 20px 0;
}
.task img{
    width: 100%;
    border-radius: 5px 5px 0 0;
}
.task_info{
    padding: 10px;
    background: white;
    border-radius: 0 0 5px 5px;
}
.task_info button{
    display: flex;
    align-items: center;
    background: rgb(252, 127, 127);
    color: white;
    float: right;
}
.task_desc{
    font-weight: 500;
    font-size: 12px;
    margin: 10px 0;
}

@media only screen and (max-width: 800px){
    .task{
        width: 45%;
    }
}
@media only screen and (max-width: 600px){
    .task{
        width: 100%;
        margin: 20px;
    }
}
.addTask_con{
    position: fixed;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    background: rgba(0, 0, 0, 0.6);
    overflow: scroll;
}
.addTask_form{
    background: white;
    flex-direction: column;
    padding: 30px;
    width: auto;
    margin: 0;
    border-radius: 10px;
}
.addTask_form .title{
    font-size: 20px;
    font-weight: 800;
    letter-spacing: 1.5px;
    color: #479CFF;
    margin-bottom: 15px;
}
.addTask_form img{
    width: 300px;    
}
.upload_img_butt{
    border: 2px dashed black;
    border-radius: 5px;
    padding: 3px 10px;
    display: flex;
    align-items: center;
    cursor: pointer;
    margin-top: 10px;
    background: rgb(136, 136, 136);
    color: white;
    font-size: 12px;
}
.addTask_form input{
    height: 30px;
}
.addTask_form .inputs{
    width: 300px;
    border: 2px solid #479CFF;
    margin-top: 20px;
}
.addTask_form .func_butt{
    background: rgb(250, 206, 110);
    border-radius: 5px;
    padding: 5px 15px;
    margin-top: 30px;
    display: flex;
    align-items: center;
}
.addTask_form .close{
    background: rgb(253, 107, 107);
    color: white;
}
@media only screen and (max-width:500px){
    .addTask_form{
        width: 100%;
        margin: 30px;
    }
    .addTask_form .inputs{
        width: 100%;
    }
}
.submissions_con{
    margin-top: 120px;
    flex-direction: column;
    padding: 20px;
}
.submissions{
    justify-content: space-evenly;
    flex-wrap: wrap;
}
.sub_related{
    flex-direction: column;
    margin: 50px;
}
@media only screen and (max-width: 700px){
    .submissions_con{
        margin-top: 150px;
    }
    .sub_related{
        margin: 30px 0;
    }
}
.submission{
    width: 30%;
    margin: 20px 0;
}
.submission img{
    width: 100%;
    border-radius: 10px 10px 0 0;
}
.submission_info{
    background: white;
    padding: 20px;
    border-radius: 0 0 10px 10px;
}
.score{
    justify-content: space-between;
}
.score button{
    font-size: 12px;
    background: rgb(242, 255, 66);
    padding: 4px 10px;
    border-radius: 3px;
}
@media only screen and (max-width: 800px){
    .submission{
        width: 45%;
    }
}
@media only screen and (max-width: 600px){
    .submission{
        width: 100%;
        margin: 20px 0;
    }
}
